import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ReviewListComponent } from './pages/review-list/review-list.component';
import { ReviewReportListComponent } from './pages/review-report-list/review-report-list.component';

const routes: Routes = [
  { path: 'reports', component: ReviewReportListComponent },
  { path: '', pathMatch: 'full', component: ReviewListComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ReviewRoutingModule {}
