import { BreakpointObserver } from '@angular/cdk/layout';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzDrawerService } from 'ng-zorro-antd/drawer';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  GetReviewListGQL,
  GetReviewListQuery,
  GetReviewListQueryVariables,
  OrderBy,
  Review,
  ReviewListFieldsFragment,
  ReviewReportStatus,
  ReviewStatus,
  SearchUsersGQL,
  User,
} from '../../../../graphql/graphql';
import { ReviewCardComponent } from '../../components/review-card/review-card.component';
import { ReviewReportsDrawerComponent } from '../../components/review-reports-drawer/review-reports-drawer.component';

enum Sort {
  UPDATED_AT_ASC = 'UPDATED_AT_ASC',
  UPDATED_AT_DESC = 'UPDATED_AT_DESC',
}

@Component({
  selector: 'app-review-list',
  templateUrl: './review-list.component.html',
  styleUrls: ['./review-list.component.scss'],
})
export class ReviewListComponent implements OnInit, OnDestroy {
  ReviewStatus = ReviewStatus;
  Sort = Sort;
  destroy$: Subject<boolean> = new Subject();
  reviews: Array<ReviewListFieldsFragment> = [];
  reviewsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  loading = false;
  dataQueryRef:
    | QueryRef<GetReviewListQuery, GetReviewListQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  searchForm: FormGroup;
  text?: string;
  status?: ReviewStatus;
  isDeleted? = false;
  sort = Sort.UPDATED_AT_DESC;
  userId?: User['id'];
  isMobile?: boolean;

  constructor(
    private fb: FormBuilder,
    private getReviewListGQL: GetReviewListGQL,
    private router: Router,
    private route: ActivatedRoute,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private searchUsersGQL: SearchUsersGQL,
    private drawerService: NzDrawerService,
    private breakpointObserver: BreakpointObserver,
  ) {
    this.searchForm = this.fb.group({
      text: [null],
      status: ['ALL'],
      isDeleted: [false],
      sort: [Sort.UPDATED_AT_DESC],
      user: [undefined],
    });
    this.isMobile = this.breakpointObserver.isMatched('(max-width: 767px)');
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);
        this.text = paramMap.get('text') || '';
        this.userId = parseInt(paramMap.get('user') || '0', 10) || undefined;
        this.status = Object.values(ReviewStatus).includes(
          paramMap.get('status') as ReviewStatus,
        )
          ? (paramMap.get('status') as ReviewStatus)
          : undefined;
        this.sort = Object.values(Sort).includes(paramMap.get('sort') as Sort)
          ? (paramMap.get('sort') as Sort)
          : Sort.UPDATED_AT_DESC;
        this.isDeleted =
          paramMap.get('deleted') === 'true'
            ? true
            : paramMap.get('deleted') === 'false'
            ? false
            : undefined;

        this.patchFormValue();

        if (this.dataSubscription) {
          this.refetch();
        } else {
          this.dataQueryRef = this.getReviewListGQL.watch(this.getVariables(), {
            notifyOnNetworkStatusChange: true,
            fetchPolicy: 'network-only',
          });
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(
              ({ data, loading, error, errors }) => {
                this.loading = loading;
                if (loading) {
                  return;
                }

                if (error || errors) {
                  this.msgService.error(
                    this.translateService.instant(
                      'ui.message.can_not_load_data',
                    ),
                  );
                  return;
                }

                if (data) {
                  this.reviews = data.reviews;
                  this.reviewsCount = data.reviewsConnection.aggregate.count;
                }
              },
              () => {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
              },
            );
        }
      });
  }

  patchFormValue(): void {
    /**
     * FIXME nz-select does not support undefined and null
     * https://github.com/NG-ZORRO/ng-zorro-antd/issues/5950
     */
    if (this.searchForm.controls.isDeleted.untouched) {
      this.searchForm.patchValue({
        isDeleted: typeof this.isDeleted === 'boolean' ? this.isDeleted : 'ALL',
      });
    }

    if (this.searchForm.controls.status.untouched) {
      this.searchForm.patchValue({
        status: this.status || 'ALL',
      });
    }

    if (this.text?.trim() && this.searchForm.controls.text.untouched) {
      this.searchForm.patchValue({
        text: this.text,
      });
    }

    if (Sort[this.sort]) {
      this.searchForm.patchValue({
        sort: this.sort || undefined,
      });
    }

    /**
     * FIXME nz-select untouched = true event user select a value
     */
    if (
      this.userId &&
      this.searchForm.controls.user.untouched &&
      this.searchForm.controls.user.pristine
    ) {
      this.searchUsersGQL
        .fetch({
          where: {
            id: this.userId,
          },
          first: 1,
        })
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ data }) => {
          if (data)
            this.searchForm.patchValue({
              user: data.users[0],
            });
        });
    }
  }

  refetch(): void {
    this.dataQueryRef?.refetch(this.getVariables());
  }

  private getVariables(): GetReviewListQueryVariables {
    return {
      where: {
        contentContains: this.text?.trim() || undefined,
        isDeleted:
          typeof this.isDeleted === 'boolean' ? this.isDeleted : undefined,
        status: this.status || undefined,
        user: {
          id: this.userId || undefined,
        },
      },
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      orderBy: {
        updatedAt:
          this.sort === Sort.UPDATED_AT_ASC ? OrderBy.Asc : OrderBy.Desc,
      },
    };
  }

  paginate(page: number): void {
    this.router.navigate([], {
      queryParams: {
        page,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  handleSearch(): void {
    const searchData = this.searchForm.value;
    const text = searchData.text || undefined;
    const sort = searchData.sort || undefined;
    const isDeleted =
      typeof searchData.isDeleted === 'boolean'
        ? searchData.isDeleted
        : undefined;
    const status = Object.values(ReviewStatus).includes(searchData.status)
      ? searchData.status
      : undefined;
    const userId = searchData.user?.id;

    this.router.navigate([], {
      queryParams: {
        text,
        sort,
        deleted: isDeleted,
        status,
        user: userId,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  viewReports(
    review: ReviewListFieldsFragment,
    reviewCard: ReviewCardComponent,
  ): void {
    const drawerRef = this.drawerService.create<
      ReviewReportsDrawerComponent,
      { reviewId: Review['id'] },
      ReviewReportStatus
    >({
      nzTitle: this.translateService.instant('review.report_list'),
      nzContent: ReviewReportsDrawerComponent,
      nzContentParams: {
        reviewId: review.id,
      },
      nzWidth: this.isMobile ? '100%' : 720,
    });
    drawerRef.afterClose.pipe(takeUntil(this.destroy$)).subscribe(() => {
      if (
        drawerRef.getContentComponent()?.action === ReviewReportStatus.Approved
      ) {
        reviewCard.status = ReviewStatus.Hidden;
      }
    });
  }

  trackById(index: number, item: any): number {
    return item.id;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
