import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  ApproveReviewReportGQL,
  RejectReviewReportGQL,
  ReviewReportListFieldsFragment,
  ReviewReportStatus,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-review-report-card',
  templateUrl: './review-report-card.component.html',
  styleUrls: ['./review-report-card.component.scss'],
})
export class ReviewReportCardComponent implements OnInit, OnDestroy {
  private destroy$: Subject<boolean>;
  ReviewReportStatus = ReviewReportStatus;
  @Input()
  report!: ReviewReportListFieldsFragment;
  status!: ReviewReportStatus;

  approving = false;
  rejecting = false;

  constructor(
    private approveReviewReportGQL: ApproveReviewReportGQL,
    private rejectReviewReportGQL: RejectReviewReportGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {
    this.destroy$ = new Subject();
  }

  ngOnInit(): void {
    this.status = this.report.status;
  }

  approve(): void {
    this.approving = true;
    this.approveReviewReportGQL
      .mutate({
        where: {
          id: this.report.id,
        },
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.approving = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('review.approve_report_error'),
            );
            return;
          }

          if (data) {
            this.status = ReviewReportStatus.Approved;
            this.msgService.success(
              this.translateService.instant('review.approve_report_success'),
            );
          }
        },
        () => {
          this.approving = false;
        },
      );
  }

  reject(): void {
    this.rejecting = true;
    this.rejectReviewReportGQL
      .mutate({
        where: {
          id: this.report.id,
        },
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.rejecting = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('review.reject_report_error'),
            );
            return;
          }

          if (data) {
            this.status = ReviewReportStatus.Rejected;
            this.msgService.success(
              this.translateService.instant('review.reject_report_success'),
            );
          }
        },
        () => {
          this.rejecting = false;
        },
      );
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
