import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  GetReviewReportListGQL,
  GetReviewReportListQuery,
  GetReviewReportListQueryVariables,
  OrderBy,
  ReportListFieldsWithReviewFragment,
  ReviewReportStatus,
  ReviewStatus,
} from '../../../../graphql/graphql';

enum Sort {
  ID_ASC = 'ID_ASC',
  ID_DESC = 'ID_DESC',
}

@Component({
  selector: 'app-review-report-list',
  templateUrl: './review-report-list.component.html',
  styleUrls: ['./review-report-list.component.scss'],
})
export class ReviewReportListComponent implements OnInit, OnDestroy {
  ReviewStatus = ReviewStatus;
  ReviewReportStatus = ReviewReportStatus;
  Sort = Sort;
  destroy$: Subject<boolean> = new Subject();
  reports: Array<ReportListFieldsWithReviewFragment> = [];
  reportsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  loading = false;
  dataQueryRef:
    | QueryRef<GetReviewReportListQuery, GetReviewReportListQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  searchForm: FormGroup;
  sort = Sort.ID_DESC;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private getReviewReportListGQL: GetReviewReportListGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private fb: FormBuilder,
  ) {
    this.searchForm = this.fb.group({
      sort: [Sort.ID_DESC],
    });
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);
        this.sort = Object.values(Sort).includes(paramMap.get('sort') as Sort)
          ? (paramMap.get('sort') as Sort)
          : Sort.ID_DESC;

        if (this.dataSubscription) {
          this.refetch();
        } else {
          this.dataQueryRef = this.getReviewReportListGQL.watch(
            this.getVariables(),
            {
              notifyOnNetworkStatusChange: true,
              fetchPolicy: 'network-only',
            },
          );
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(
              ({ data, loading, error, errors }) => {
                this.loading = loading;
                if (loading) {
                  return;
                }

                if (error || errors) {
                  this.msgService.error(
                    this.translateService.instant(
                      'ui.message.can_not_load_data',
                    ),
                  );
                  return;
                }

                if (data) {
                  this.reports = data.reviewReports;
                  this.reportsCount =
                    data.reviewReportsConnection.aggregate.count;
                }
              },
              () => {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
              },
            );
        }
      });
  }

  refetch(): void {
    this.dataQueryRef?.refetch(this.getVariables());
  }

  private getVariables(): GetReviewReportListQueryVariables {
    return {
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      orderBy: {
        id: this.sort === Sort.ID_DESC ? OrderBy.Desc : OrderBy.Asc,
      },
    };
  }

  paginate(page: number): void {
    this.router.navigate([], {
      queryParams: {
        page,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  handleSearch(): void {
    this.router.navigate([], {
      queryParams: {
        sort: this.searchForm.get('sort')?.value || undefined,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  trackBy(index: number, report: ReportListFieldsWithReviewFragment): number {
    return report.id;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
