import {
  AfterViewInit,
  Component,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';
import 'lg-zoom.js';
import 'lg-thumbnail.js';

import {
  ApproveReviewGQL,
  RejectReviewGQL,
  ReviewListFieldsFragment,
  ReviewStatus,
} from '../../../../graphql/graphql';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { NzMessageService } from 'ng-zorro-antd/message';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-review-card',
  templateUrl: './review-card.component.html',
  styleUrls: ['./review-card.component.scss'],
})
export class ReviewCardComponent implements OnInit, OnDestroy, AfterViewInit {
  private destroy$: Subject<boolean>;
  ReviewStatus = ReviewStatus;
  @Input()
  review!: ReviewListFieldsFragment;
  status!: ReviewStatus;

  rejecting = false;
  approving = false;

  constructor(
    private approveReviewGQL: ApproveReviewGQL,
    private rejectReviewGQL: RejectReviewGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {
    this.destroy$ = new Subject();
  }

  ngOnInit(): void {
    this.status = this.review.status;
  }

  ngAfterViewInit(): void {
    if (this.review.images?.length) {
      $(`#gallery_${this.review.id}`)
        .justifiedGallery()
        .on('jg.complete', () => {
          lightGallery(document.getElementById(`gallery_${this.review.id}`), {
            thumbnail: true,
          });
        });
    }
  }

  viewProductThumbnail(): void {
    lightGallery(
      document.getElementById(`review_card_lg_${this.review.product.id}`),
      {
        dynamic: true,
        dynamicEl: [
          {
            src: this.review.product.thumbnail?.url,
          },
        ],
      },
    );
  }

  approve(): void {
    this.approving = true;
    this.approveReviewGQL
      .mutate({
        where: {
          id: this.review.id,
        },
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.approving = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('review.approve_error'),
            );
            return;
          }

          if (data) {
            this.status = ReviewStatus.Approved;
            this.msgService.success(
              this.translateService.instant('review.approve_success'),
            );
          }
        },
        () => {
          this.approving = false;
        },
      );
  }

  reject(): void {
    this.rejecting = true;
    this.rejectReviewGQL
      .mutate({
        where: {
          id: this.review.id,
        },
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.rejecting = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('review.hide_error'),
            );
            return;
          }

          if (data) {
            this.status = ReviewStatus.Hidden;
            this.msgService.success(
              this.translateService.instant('review.hide_success'),
            );
          }
        },
        () => {
          this.rejecting = false;
        },
      );
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
    if (this.review.images?.length) {
      $(`#gallery_${this.review.id}`).justifiedGallery('destroy');
      (window as any).lgData[
        document
          .getElementById(`gallery_${this.review.id}`)
          ?.getAttribute('lg-uid') || ''
      ]?.destroy(true);
    }
  }
}
