import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzDrawerRef } from 'ng-zorro-antd/drawer';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  ApproveReviewReportGQL,
  GetReportsOfReviewGQL,
  GetReportsOfReviewQuery,
  GetReportsOfReviewQueryVariables,
  OrderBy,
  RejectReviewReportGQL,
  Review,
  ReviewReportListFieldsFragment,
  ReviewReportStatus,
} from '../../../../graphql/graphql';

const PAGE_SIZE = 10;

@Component({
  selector: 'app-review-reports-drawer',
  templateUrl: './review-reports-drawer.component.html',
  styleUrls: ['./review-reports-drawer.component.scss'],
})
export class ReviewReportsDrawerComponent implements OnInit, OnDestroy {
  ReviewReportStatus = ReviewReportStatus;
  destroy$: Subject<boolean>;
  @Input()
  reviewId!: Review['id'];
  private dataRef?: QueryRef<
    GetReportsOfReviewQuery,
    GetReportsOfReviewQueryVariables
  >;
  loading = false;
  completed = false;
  reports: Array<ReviewReportListFieldsFragment & { loading?: boolean }> = [];
  list: Array<ReviewReportListFieldsFragment & { loading?: boolean }> = [];
  approving = false;
  rejecting = false;
  action?: ReviewReportStatus.Approved;

  constructor(
    private drawerRef: NzDrawerRef<Review['id']>,
    private getReportsOfReviewGQL: GetReportsOfReviewGQL,
    private approveReviewReportGQL: ApproveReviewReportGQL,
    private rejectReviewReportGQL: RejectReviewReportGQL,
    private translateService: TranslateService,
    private msgService: NzMessageService,
  ) {
    this.destroy$ = new Subject();
  }

  ngOnInit(): void {
    this.dataRef = this.getReportsOfReviewGQL.watch(
      {
        where: {
          review: {
            id: this.reviewId,
          },
        },
        first: PAGE_SIZE,
        orderBy: {
          createdAt: OrderBy.Desc,
        },
      },
      {
        fetchPolicy: 'network-only',
        notifyOnNetworkStatusChange: true,
      },
    );
    this.dataRef.valueChanges
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data, error, errors, loading }) => {
        this.loading = loading;
        if (loading) {
          return;
        }

        if (error || errors) {
          return;
        }

        if (data?.reviewReports?.length) {
          this.reports = [
            ...data.reviewReports.map((report) => ({
              ...report,
            })),
          ];
          this.list = this.reports;
        }

        this.completed =
          data?.reviewReports?.length === 0 ||
          data?.reviewReports?.length < PAGE_SIZE;
      });
  }

  refetch(): void {
    this.list = [...this.list, ...new Array(5).fill({ loading: true })];
    this.dataRef?.refetch({
      where: {
        review: {
          id: this.reviewId,
        },
      },
      first: PAGE_SIZE,
      skip: this.reports.length,
      orderBy: {
        createdAt: OrderBy.Desc,
      },
    });
  }

  approve(report: ReviewReportListFieldsFragment): void {
    this.approving = true;
    this.approveReviewReportGQL
      .mutate({
        where: {
          id: report.id,
        },
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.approving = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('review.approve_report_error'),
            );
            return;
          }

          if (data) {
            report.status = ReviewReportStatus.Approved;
            this.action = ReviewReportStatus.Approved;
            this.msgService.success(
              this.translateService.instant('review.approve_report_success'),
            );
          }
        },
        () => {
          this.approving = false;
        },
      );
  }

  reject(report: ReviewReportListFieldsFragment): void {
    this.rejecting = true;
    this.rejectReviewReportGQL
      .mutate({
        where: {
          id: report.id,
        },
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ data, errors }) => {
          this.rejecting = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('review.reject_report_error'),
            );
            return;
          }

          if (data) {
            report.status = ReviewReportStatus.Rejected;
            this.msgService.success(
              this.translateService.instant('review.reject_report_success'),
            );
          }
        },
        () => {
          this.rejecting = false;
        },
      );
  }

  close(): void {
    this.drawerRef.close(this.action);
  }

  trackBy(index: number, report: ReviewReportListFieldsFragment): number {
    return report.id;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
